body {
    background: #454545;
}

h1 {
    color: #fff;
    padding: 10px 0;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(100, 100, 100, 20);
    transition: 0.1s;
    border: none;


}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 10);
}

a {
    color: initial;
}

a:hover {
    text-decoration: initial;
}

.text-muted i {
    margin: 0 10px;
}